.titlebox {
    background-image: url('https://cdn.inquisitr.com/wp-content/uploads/2016/12/attack-on-titan-season-2-release-date.jpg');
    background-size: 100%;
    background-repeat: no-repeat;
    width: 100%;
    min-height: 280px;
    text-align: center;
    vertical-align: center;
    font-family: 'Holtwood One SC', cursive;
    color: #fff;
    text-shadow: 0 0 5px #000;
    margin-bottom: 20px;
}

.titlebox h1 {
    margin-top: 90px;
    font-size: 3.5em;
}

.titlebox p {
    font-size: 1.8em;
}

